@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css?family=Seymour+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Andika&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gloria+Hallelujah&display=swap);
@font-face {
  font-family: 'PermanentMarker';
  src: local('PermanentMarker'), url(/static/media/PermanentMarker-Regular.c863f802.ttf) format('truetype');
}
@font-face {
  font-family: 'GloriaHallelujah';
  src: local('GloriaHallelujah'), url(/static/media/GloriaHallelujah-Regular.88f59255.ttf) format('truetype');
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'), url(/static/media/Montserrat-VariableFont_wght.52a37115.ttf) format('truetype');
}
@font-face {
  font-family: 'MontserratItalic';
  src: local('MontserratItalic'), url(/static/media/Montserrat-Italic-VariableFont_wght.5b315bd5.ttf) format('truetype');
}



*{
  margin:0;
}

body {
  margin: 0;
  padding: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, 'Quicksand', 'Andika', 'Seymour One', 'Gloria Hallelujah'  ;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
}




